import React from 'react';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import Header from './pages/Header';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';

import RouteWithSubRoutes from './pages/RouteWithSubRoutes'
import routes from './pages/router.config'


const App = ()=>{
	console.log(routes);
	
	return <Router>
				<Header />
				<Switch>
					{routes.map((route, i) => (
						<RouteWithSubRoutes key={i} {...route} />
					))}
				</Switch>
			</Router>
}

export default App;

