import React, {component} from 'react'
import {Redirect, Route, NavLink, Switch } from 'react-router-dom'

//权限路由
//封装一个组件
//PrivateRoute替代Route
//进入PrivateRoute页面的时候，如果不是登录状态，就转到登录页
//Com就是重命名，改大写，组件首字母


const PrivateRoute = ({component:Com, ...rest}) => {
	return (
		<Route
			{ ...rest }
				render={({location}) => 
					localStorage.getItem('userinfo')
					? <Com {...rest}/>
					: (<Redirect to={{ pathname:'/login', state:location }} /> )
				}
		/>
	)
}

export default PrivateRoute

