import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';

//import {Core, Guide, Api, Hooks} from './Doc';
import Core from './Core'
import Guide from './Guide'
import Api from './Api'
import Hooks from './Hooks'




const routes = [
	{
		path:'/',
		exact:true,
		component:Home
	},
	{
		path:'/doc',
		component:Doc,
		pri:true,
		routes:[
			{
				path:'/doc/core',
				component:Core
			},
			{
				path:'/doc/guide',
				component:Guide
			},
			{
				path:'/doc/api',
				component:Api
			},
			{
				path:'/doc/hooks',
				component:Hooks
			}
		]
	},
	{
		path:'/tuto',
		exact:false,
		component:Tuto,
		pri:true
	},
	{
		path:'/blog',
		exact:false,
		component:Blog
	},
	{
		path:'/comm',
		exact:false,
		component:Comm
	},
	{
		path:'/login',
		exact:false,
		component:Login
	}
]

export default routes;